@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600;700&display=swap');

$red: #ff3838;


* {
    font-family: 'Nunito', sans-serif;
    margin:0; padding:0;
    box-sizing: border-box;
    outline: none; border:none;
    text-decoration: none;
    text-transform: capitalize;
    transition:all .2s linear;
 
    &::selection{
        background: $red;
        color:#fff;
    }
}
  
html {
    font-size: 62.5%;
    overflow-x: hidden;
    scroll-behavior: smooth;
    scroll-padding-top: 6rem;
}
  
body{
    background:#f7f7f7;
}
  
section{
    padding:2rem 9%;
}

.header{
    position: fixed;
    top:0; left: 0; right:0;
    z-index: 1000;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background:#fff;
    padding:2rem 9%;
    box-shadow: 0 .5rem 1rem rgba(0,0,0,.1);
  
    &__logo {
        font-size: 2.5rem;
        font-weight: bolder;
        color:#666;

        & i {
            padding-right: .5rem;
            color: $red;
        }
    }

    &__navbar {

        & a {
            font-size: 2rem;
            margin-left: 2rem;
            color:#666;

            &:hover {
                color: $red;
            }
        }
    }

    #menu-bar {
        font-size: 3rem;
        cursor: pointer;
        color:#666;
        border:.1rem solid #666;
        border-radius: .3rem;
        padding:.5rem 1.5rem;
        display: none;
    }
}

@media(max-width:991px){

    html{
        font-size: 55%;
    }
  
    header{
        padding:2rem;
    }
  
    section{
        padding:2rem;
    } 
}


.home {

    display: flex;
    flex-wrap: wrap;
    gap:1.5rem;
    min-height: 100vh;
    align-items: center;
    background:url(../src/img/home-bg.jpg) no-repeat;
    background-size: cover;
    background-position: center;
    
    &__content {
        flex:1 1 40rem;
        padding-top: 6.5rem;

        & h3 {
            font-size: 5rem;
            color:#333;
        }

        & p {
            font-size: 1.7rem;
            color:#666;
            padding:1rem 0;
        }      
    }

    &__image {
        flex: 1 1 40rem; 
        
        & img {
            width:100%;
            padding:1rem;
            animation:float 3s linear infinite;
        }
    }

    @keyframes float{
        0%, 100%{
            transform: translateY(0rem);
        }
        50%{
            transform: translateY(3rem);
        }
    }
}

.btn{
    display: inline-block;
    padding:.8rem 3rem;
    border:.2rem solid $red;
    color: $red;
    cursor: pointer;
    font-size: 1.7rem;
    border-radius: .5rem;
    position: relative;
    overflow: hidden;
    z-index: 0;
    margin-top: 1rem;

    &::before {
        content: '';
        position: absolute;
        top:0; right: 0;
        width:0%;
        height:100%;
        background: $red;
        transition: .3s linear;
        z-index: -1;
    }

    &:hover {
        background: $red;
        color:#fff;

        &::before {
            width:100%;
            left: 0;
        }
    }
}

.heading {
    text-align: center;
    font-size: 3.5rem;
    padding:1rem;
    color:#666;

    & span {
        color: $red;
    }
}

.container {
    display: flex;
    flex-wrap: wrap;
    gap:1.5rem;
}

.box {
    flex:1 1 30rem;
    position: relative;
    overflow: hidden;
    box-shadow: 0 .5rem 1rem rgba(0,0,0,.1);
    border:.1rem solid rgba(0,0,0,.3);
    cursor: pointer;
    border-radius: .5rem;

    &:hover {
        & .box {
        
            &__image {
                top: 0;
            }

            &__content{
                transform: translateY(100%);
            }
        }
    }

    &__content {
        text-align: center;
        background:#fff;
        padding:2rem;

        & img {
            margin:1.5rem 0;
        }

        & h3 {
            font-size: 2.5rem;
            color:#333;
        }

        & p {
            font-size: 1.6rem;
            color:#666;
            padding:1rem 0;
        }
    }

    &__image {
        height:100%;
        width:100%;
        object-fit: cover;
        position: absolute;
        top:-100%; left:0;
    }
}

.popular {

    &__box {
        padding:2rem;
        background:#fff;
        box-shadow: 0 .5rem 1rem rgba(0,0,0,.1);
        border:.1rem solid rgba(0,0,0,.3);
        border-radius: .5rem;
        text-align: center;
        flex:1 1 30rem;
        position: relative;

        & h3{
            color:#333;
            font-size: 2.5rem;
            padding-top: 1rem;
        }

        & img{
            height:25rem;
            object-fit: cover;
            width:100%;
            border-radius: .5rem;
        }
    }

    &__price {
        position: absolute;
        top:3rem; left:3rem;
        background: $red;
        color:#fff;
        font-size: 2rem;
        padding:.5rem 1rem;
        border-radius: .5rem;
    }

    &__stars i {
        color:gold;
        font-size: 1.7rem;
        padding:1rem .1rem;
    }
}

.steps {
    display: flex;
    flex-wrap: wrap;
    gap: 1.5rem;
    padding: 1rem;
      
    &__box {
        flex: 1 1 25rem;
        padding: 1rem;
        text-align: center;

        & img {
            border-radius: 50%;
            border:1rem solid #fff;
            box-shadow: 0 .5rem 1rem rgba(0,0,0,.1);
        }

        & h3 {
            font-size: 3rem;
            color:#333;
            padding: 1rem;
        }
    }     
}

.gallery {

    &__box {
        height:25rem;
        flex:1 1 30rem;
        border:1rem solid #fff;
        box-shadow: 0 .5rem 1rem rgba(0,0,0,.1);
        border-radius: .5rem;
        position: relative;
        overflow: hidden;

        & img {
            height:100%;
            width:100%;
            object-fit: cover;
        }

        &:hover .gallery__content {
            top: 0;
        }
    }

    &__content {
        position: absolute;
        top:-100%; left:0;
        height: 100%;
        width:100%;
        background:rgba(255,255,255,.9);
        padding:2rem;
        padding-top: 5rem;
        text-align: center;

        & h3 {
            font-size: 2.5rem;
            color:#333;
        }

        & p {
            font-size: 1.5rem;
            color:#666;
            padding:1rem 0;
        }
    }
}

.review {

    &__box {
        text-align: center;
        padding:2rem;
        border:1rem solid #fff;
        box-shadow: 0 .5rem 1rem rgba(0,0,0,.3);
        border-radius: .5rem;
        flex:1 1 30rem;
        background:#333;
        margin-top: 6rem;

        & img {
            height: 12rem;
            width:12rem;
            border-radius: 50%;
            border:1rem solid #fff;
            margin-top: -8rem;
            object-fit: cover;
        }

        & h3 {
            font-size: 2.5rem;
            color:#fff;
            padding:.5rem 0;
        }

        & p {
            font-size: 1.5rem;
            color:#eee;
            padding:1rem 0;
        }
    }

    &__stars i {
        font-size: 2rem;
        color: $red;
        padding:.5rem 0;
    }
}  

.order {

    &__row {
        padding:2rem;
        box-shadow: 0 .5rem 1rem rgba(0,0,0,.1);
        background:#fff;
        display: flex;
        flex-wrap: wrap;
        gap:1.5rem;
        border-radius: .5rem;

        & form {
            flex:1 1 50rem;
            padding:1rem;

            & textarea {
                padding:1rem;
                margin:1rem 0;
                font-size: 1.7rem;
                color:#333;
                text-transform: none;
                border:.1rem solid rgba(0,0,0,.3);
                border-radius: .5rem;
                width:100%;
                resize: none;
                height:15rem;
            }

            & .btn {
                background:none;

                &:hover {
                    background: $red;
                }
            }
        }
    }

    &__image {
        flex:1 1 30rem;

        & img {
            height: 100%;
            width:100%;
            object-fit: cover;
            border-radius: .5rem;
        }
    }

    &__input-box {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;

        & input {
            padding:1rem;
            margin:1rem 0;
            font-size: 1.7rem;
            color:#333;
            text-transform: none;
            border:.1rem solid rgba(0,0,0,.3);
            border-radius: .5rem;
            width:49%;
        }
    }
} 
  
.footer {
    background:#000;
    text-align: center;

    &__links {
        display: flex;
        gap:1.5rem;
        justify-content: center;
        flex-wrap: wrap;
    }

    &__credit {
        padding:2.5rem 1rem;
        color:#fff;
        font-weight: normal;
        font-size: 2rem;

        & span {
            color: $red;
        }
    }
}

#scroll-top {
    position: fixed;
    top:-120%;
    right:2rem;
    padding:.5rem 1.5rem;
    font-size: 4rem;
    background:var(--red);
    color:#fff;
    border-radius: .5rem;
    transition: 1s linear;
    z-index: 1000;

    &.active {
        top:calc(100% - 12rem)
    }
}
  
.loader-container {
    position: fixed;
    top:0; left:0;
    z-index: 10000;
    background:#fff;
    display: flex;
    align-items: center;
    justify-content: center;
    height:100%;
    width:100%;

    &.fade-out {
        top: 120%;
    }
}  

@media(max-width:991px) {

    html {
        font-size: 55%;
    }
  
    header {
        padding:2rem;
    }
  
    section {
        padding:2rem;
    }
}

@media(max-width:768px) {

    #menu-bar {
      display: block !important;
    }
  
    .header {

        &__navbar{
            position: absolute;
            top:100%; left: 0; right:0;
            background: #f7f7f7;
            border-top: .1rem solid rgba(0,0,0,.1);
            clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);

            &.active {
                clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);          
            }

            & a {
                margin: 1.5rem;
                padding:1.5rem;
                display: block;
                border:.2rem solid rgba(0,0,0,.1);
                border-left:.5rem solid $red;
                background:#fff;
            }
        }
    }
}
  
@media(max-width:450px){
  
    html{
        font-size: 50%;
    }

    .order__input-box input {
        width: 100%;
    }
}  
  
  
